import * as React from 'react';
import '../css/App.css';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import NoSsr from '@material-ui/core/NoSsr';
import Tab from '@material-ui/core/Tab';

const styles = theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper
  }
});
class NavigationBar extends React.Component {
  state = {
    value: 0
  };

  handleChange = (event, value) => {
    this.setState({ value });
  };
  render() {
    const { classes } = this.props;
    const { value } = this.state;
    return (
      <NoSsr>
        <div className={classes.root}>
          <AppBar position="static">
            <Tabs fullWidth value={value} onChange={this.handleChange}>
              <Tab label="Top" exact to="/portfolio/" component={Link} />
              <Tab label="Profile" to="/portfolio/profile" component={Link} />
              <Tab label="Works" to="/portfolio/works" component={Link} />
              <Tab label="Skills" to="/portfolio/skills" component={Link} />
              <Tab label="Contacts" to="/portfolio/contacts" component={Link} />
            </Tabs>
          </AppBar>
        </div>
      </NoSsr>
    );
  }
}
NavigationBar.propTypes = {
  classes: PropTypes.object.isRequired
};
export default withStyles(styles)(NavigationBar);
