import * as React from 'react';
import './css/App.css';
import { Route, Switch } from 'react-router-dom';

import NavigationBar from './components/NavigationBar';
import Profile from './components/Profile';
import Top from './components/Top';
import Works from './components/Works';
import Skills from './components/Skills';
import Contacts from './components/Contacts';
import LoginPage from './components/LoginPage';
import PrivateRoute from './components/PrivateRoute';

class App extends React.Component {
  render() {
    return (
      <div className="App">
        <NavigationBar />
        <Switch>
          <PrivateRoute exact path="/portfolio/" component={Top} />
          <Route path="/portfolio/login" component={LoginPage} />
          <PrivateRoute path="/portfolio/profile" component={Profile} />
          <PrivateRoute path="/portfolio/works" component={Works} />
          <PrivateRoute path="/portfolio/skills" component={Skills} />
          <PrivateRoute path="/portfolio/contacts" component={Contacts} />
          <Route component={() => <h2>Not Found</h2>} />
        </Switch>
      </div>
    );
  }
}

export default App;
